function showForm(){
    const form = document.getElementById("new-trip-section");
    form.style.display = "block";
}

function hideForm(){
    const form = document.getElementById("new-trip-section");
    form.style.display = "none";
}

function saveTrip(){
    console.log("Trying to save trip....");
    const data = {
        city: document.getElementById("city").value,
        state: document.getElementById("state").value,
        country: document.getElementById("country").value
    }
    postData(data)
    .then(function(res){
        console.log("----------Data received on client side!--------")
        console.log(res);
    })
}

const postData = async function(data = {}){
    const response = await fetch('http://localhost:8000/submitForm', {
        method: 'POST',
        credentials: "same-origin",
        headers:{ 
            'Content-Type': 'application/json'
        },
        body: JSON.stringify(data),
    });
    try{
        const data = await response.json();
        return data;
    } catch(error){
        console.log("There was an error: " + error);
    }
}

export { saveTrip }